.image {
    width: 10vw;
}

table {
    margin-top: 20px;
    table-layout: fixed;
}

.green {
    color: #27ae60;
}

.yellow {
    color: #f39c12;
}

.red {
    color: #c0392b;
}

.blue {
    color: #2980b9;
}

.purple {
    color: #8e44ad;
}

.gameContainer {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.legend {
    text-align: center;
    margin-top: 20px;
    border: 1px solid grey;
    border-radius: 5px;
    margin-bottom: 20px;
}